.assistance {
  padding: 80px 0 160px 0;
  position: relative;
}

.assistance-faq {
}

.assistance-faq__item {
  margin-bottom: 32px;
}

.assistance-faq__question {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
}

.assistance-faq__answer {
  margin-top: 20px;
}

.bg-block__assistance {
  position: absolute;
  z-index: -2;
  right: 100px;
  top: 0;
}

.circle__assistance {
  top: 0;
  right: 100px;
  border-radius: 50%;
  position: absolute;
  z-index: -2;
  width: 441px;
  height: 441px;
  background: #FFFFFF;
  border: 10px solid #FFFFFF;
  box-shadow: 20px 20px 30px rgba(70, 156, 243, 0.02), -20px -20px 30px rgba(236, 236, 236, 0.25), inset 16px 16px 30px rgba(70, 156, 243, 0.03), inset -16px -16px 30px rgba(255, 255, 255, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;

  div {
    border-radius: 50%;
    width: 315px;
    height: 315px;
    background: rgba(50, 193, 146, 0.2);
    box-shadow: 20px 20px 30px rgba(70, 156, 243, 0.02), -20px -20px 30px rgba(236, 236, 236, 0.25), inset 16px 16px 30px rgba(70, 156, 243, 0.03), inset -16px -16px 30px rgba(255, 255, 255, 0.25);
  }
}